import React from "react"
import { FaRegEnvelope } from "react-icons/fa"
import { useForm } from "react-hook-form"
import LogoSGSFooter from "@/assets/logo_sgs_footer.png"
import { BottomBar } from "./BottomBar"
import { ContactComponent } from "../ContactComponent"
import { Link } from "gatsby"
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "../ui/form"
import { Input } from "../ui/input"
import { Button } from "../ui/button"
import { SocialMedia } from "../SocialMedia"
import { z } from "zod"
import { zodResolver } from "@hookform/resolvers/zod"
import { ContactForm } from "../services/contact-form"
import { useToast } from "@/hooks/use-toast"

export type FooterMenuItem = {
  path: string
  title: string
}

const formSchema = z.object({
  email: z.string().email({
    message: "Por favor, insira um endereço de e-mail válido.",
  }),
})

const Footer = ({ menuItems }: { menuItems: FooterMenuItem[] }) => {
  const { toast } = useToast()

  const subscribeNewsletter = async (data: z.infer<typeof formSchema>) => {
    try {
      await ContactForm.newsletter(data.email)
      toast({
        title: "Sucesso!",
        description: "Inscrição realizada com sucesso.",
        variant: "default",
      })
    } catch (error) {
      toast({
        variant: "destructive",
        title: "Erro",
        description: "Ocorreu um erro ao se inscrever na newsletter.",
      })
    }
  }

  const form = useForm({
    resolver: zodResolver(formSchema),
  })

  return (
    <footer className="bg-white text-white">
      <div className="bg-primary-500 py-10">
        <div className="container mx-auto px-4 lg:max-w-[1320px]">
          <div className="grid grid-cols-1 gap-8 md:grid-cols-2 lg:grid-cols-10">
            {/* First column */}
            <div className="space-y-6 lg:col-span-4">
              <Form {...form}>
                <form onSubmit={form.handleSubmit(subscribeNewsletter)}>
                  <FormField
                    control={form.control}
                    name="email"
                    render={({ field }) => (
                      <FormItem className="flex flex-col">
                        <FormLabel className="mb-2 text-center text-lg font-semibold uppercase lg:text-start">
                          Novidades e ofertas por email
                        </FormLabel>
                        <div className="flex">
                          <div className="relative flex-grow">
                            <div className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">
                              <FaRegEnvelope className="text-white" />
                            </div>
                            <FormControl>
                              <Input
                                className="h-[40px] rounded-none pl-10 placeholder:text-white autofill:bg-primary-500"
                                placeholder="Insira seu melhor e-mail"
                                {...field}
                              />
                            </FormControl>
                          </div>
                          <Button
                            type="submit"
                            className="h-[40px] rounded-none bg-white uppercase text-primary-700"
                          >
                            Enviar
                          </Button>
                        </div>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                </form>
              </Form>
              <SocialMedia
                theme="dark"
                variant="always"
                size="lg"
                className="mx-auto w-full justify-center lg:justify-start"
              />
              <div className="flex flex-col items-center justify-center text-white lg:items-start">
                <h3 className="mb-2 text-sm font-semibold uppercase">
                  Endereço
                </h3>
                <p className="text-center text-sm lg:text-start">
                  Rua Hamilton Fernandes, 56 - Sônia Maria - Mauá-SP - CEP:
                  09380-390
                </p>
              </div>
            </div>

            <div className="border-t-hairline border-white pt-4 lg:col-span-2 lg:border-l-hairline lg:border-t-0 lg:pl-8">
              <ul className="flex flex-col items-start space-y-3 text-white">
                {menuItems.map(link => (
                  <li key={link.title} className="first-of-type:font-bold">
                    <Link to={link.path} className="uppercase text-white">
                      {link.title}
                    </Link>
                  </li>
                ))}
              </ul>
            </div>

            <div className="border-t-hairline border-white pt-4 lg:col-span-2 lg:border-l-hairline lg:border-t-0 lg:pl-8">
              <h3 className="mb-4 text-start text-lg font-semibold uppercase lg:text-start">
                Contatos
              </h3>
              <div className="mx-auto flex w-full flex-col items-center justify-center gap-4 lg:items-start">
                <ContactComponent.Whatsapp />
                <ContactComponent.Phone />
              </div>
            </div>

            <div className="border-t-hairline border-white pt-4 md:mt-0 lg:col-span-2 lg:border-t-0">
              <h3 className="mb-4 text-start text-lg font-semibold uppercase lg:text-start">
                Certificado
              </h3>
              <div className="flex justify-center lg:justify-start">
                <img
                  width="120"
                  height="100"
                  src={LogoSGSFooter}
                  alt="Empresa com certificação ISO 9001"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <BottomBar />
    </footer>
  )
}

export default Footer
